import uniStatusBar from "@/components/uni-status-bar/uni-status-bar"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniStatusBar,
		uniLoadMore
	},
	data(){
		return{
			id:'',
			url:'',
			active:1,
			rangeActive:'',
			sh:'',
			dataList:[],
			page:1,
			pageSize:5,
			total:0,
			more:'',
			basic:'',
			carouselList:[]
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   onLoad(e){
		   this.id=e.id
	   },
	mounted(){
		var that=this
		this.url='&r=api.merchant.home.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+this.active
		this.getBasic()
		this.getCarousel()
		this.getList(this.page)
		setTimeout(function(){
			that.$getHeight('#sv',(res) =>{
				that.sh=res
			})
		},0)
	},
	methods:{
		share(){
			console.log('share')
		},
		search(){
			console.log('search')
		},
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page)
		},
		toggleRange(t){
			this.rangeActive=t
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
			if(this.active!=4){
				this.rangeActive=''
				this.url='&r=api.merchant.home.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+this.active
			}else{
				this.rangeActive= this.rangeActive == 1 ? 2:1
				if(this.rangeActive==1){
					this.url='&r=api.merchant.home.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+4
				}else if(this.rangeActive==2){
					this.url='&r=api.merchant.home.goods&page='+this.page+'&pagesize='+this.pageSize+'&sort='+5
				}
			}
		},
		getList(p){
			var that=this
			// var params={
			//   page:p,
			//   pagesize: this.pageSize
			// }
			if(this.page==1){
				this.$loading()
			}
			  // var url='/wangtosale_list'
			  this.$apiPost(this.url).then((res) =>{
				  that.total=res.total
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		getBasic(){
			var that=this
			  var url='&r=api.merchant.home&merchid='+this.id
			  this.$apiPost(url).then((res) =>{
				  that.basic=res.data
			  })
		},
		getCarousel(){
			var that=this
			  var url='&r=api.merchant.home.banner&merchid='+this.id
			  this.$apiPost(url).then((res) =>{
				  that.carouselList=res.data
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
	}
}
